import axios from './request'

// 
export const getRequestImg = (url, tokenId, contractAddress, chainId) => {
    return new Promise((resolve, reject) => {
        axios.agentNFT(url)
            .then(function (data) {
                // console.log(data, 'data1');
                if(data.image || data.image_url) {
                    resolve({ metaData: data, image: data.image || data.image_url })
                } else {
                    reject('Can not find image source.' || intl.get('canotSource')) // 'Can not find image source.'
                }
            }).catch(function (ex) {
                // eslint-disable-next-line
                if (chainId == 1 || chainId == 137) { // 
                    console.log('');
                    let url = `https://api.opensea.io/api/v1/asset/${contractAddress}/${tokenId}`
                    axios.agentDOM(url)
                        .then(function (data) {
                            // html
                            const content = document.createElement('div');
                            content.innerHTML = data;
                            // html
                            const img = content.getElementsByClassName('content-main')[0].getElementsByTagName('a')[0].href;
                            // html
                            const dataTxt = content.getElementsByClassName('content-main')[0].getElementsByClassName('response-info')[0].innerHTML;
                            // 
                            let nameStartLen = dataTxt.indexOf('"name"');
                            let name = ''
                            if (nameStartLen !== -1) {
                                // 
                                let nameStart = dataTxt.substring(nameStartLen + 84)
                                // 
                                name = nameStart.substring(0, nameStart.indexOf('"'))
                            };
                            console.log('', img);
                            resolve({
                                metaData: {
                                    name
                                }, image: img
                            })
                        }).catch(function (ex) {
                            console.error(ex);
                        })
                } else { // 
                    reject(ex)
                }
            })
    })
}

// 
export const getImgData = async (tokenURI, tokenId, contractAddress, chainId) => {
    let type = tokenURI.trim().substring(0, 4);
    let meta_Data = {}
    let requestImgUrl = '';
    //  tokenUrl 
    
    if (type === 'http') {
        // nft
        let { metaData, image } = await getRequestImg(tokenURI, tokenId, contractAddress, chainId);
        requestImgUrl = image;
        meta_Data = metaData
    } else if (type === 'ipfs') {
        // nft
        let { metaData, image } = await getRequestImg(`https://ipfs.io/${tokenURI.replace(':/', '').replace("ipfs/ipfs/", 'ipfs/')}`, tokenId, contractAddress, chainId);
        requestImgUrl = image;
        meta_Data = metaData
    } else {
        // nft
        let { metaData, image } = tokenURIImage(tokenURI);
        
        requestImgUrl = image;
        meta_Data = metaData
    }

    // 
    const imgType = requestImgUrl.substring(0, 4);
    if (imgType === 'ipfs') {
        requestImgUrl = `https://ipfs.io/${requestImgUrl.replace(':/', '').replace("ipfs/ipfs/", 'ipfs/')}`;
    }

    return {
        meta_Data,
        requestImgUrl
    }
}
